import './App.css';
import {body,contenedorDiv,pizza,altura} from './components/style'

import { Outlet } from "react-router";

const App = () => {

  
  

  return (
    <>
    <div className="container-fluid">
      <div className="row" style={altura}>
        <div className="image col-lg-5 col-12 d-flex justify-content-center align-items-center" style={contenedorDiv}><img src="https://pngimg.com/uploads/pizza/pizza_PNG44073.png" alt="imagen-pizza" style={pizza}  /></div>
        <div style={body} className="contenido col-lg-7 col-12"><Outlet/></div>
      </div>
    </div>
    
    </>
  )
}

export default App
