import { useFetch } from "../components/hooks/useFetch"
import { Link } from "react-router-dom";
import { enlaceFix,animacionMotion } from "../components/style"
import NavBar from "../components/NavBar";
import { motion } from "framer-motion"
import Data from "./data.json"

const Inicio = () => {

    

    return (
        <>
        <NavBar/>
        <motion.div
            initial="out"
            animate="in"
            exit="out"
            variants={animacionMotion} className="container mt-5">
            <div className="row">
            {Data.map((item) => (
            <div className="col-lg-12" key={item.id}>
                
                    <Link style={enlaceFix} to={`/inicio/${item.id}`} className="card mx-3 my-3">
                        <div className="row g-0">
                            <div className="col-md-4">
                                <img src={item.icono} className="img-fluid rounded-start" alt={"imagen de " + item.nombre}/>
                            </div>
                            <div className="col-md-8">
                                <div className="card-body">
                                    <h5 className="card-title" style={enlaceFix}>{item.nombre}</h5>
                                    <p className="card-text text-muted" style={enlaceFix}>{item.descripcion}</p>
                                    <p className="card-text"><small style={enlaceFix}>{item.direccion}</small></p>
                                </div>
                            </div>
                        </div>
                    
                    </Link>
            </div>    
            ))}
            </div>
        </motion.div>
        </>
           )
}

export default Inicio


