import { Link } from "react-router-dom"
import { error, tituloError,animacionMotion } from "../components/style"
import { motion } from "framer-motion"

const Error = () => {
    return (
        <motion.div
        initial="out"
        animate="in"
        exit="out" variants={animacionMotion} style={error}>
            <h1 style={tituloError}>Pgina no encontrada</h1>
            <p>Asegurese que esta ingresando en una pgina vlida.</p>
            <Link to="/" className="btn btn-outline-dark">Iniciar sesin</Link>
        </motion.div>
    )
}

export default Error
