import { useParams } from "react-router"
import Data from "./data.json"
import { fixSize,contenedor,carta,sneaker,sneakerImg,circle,infoH1,infoH3,sizes,sizesButton,botonFace,botonInsta,animacionMotion } from "../components/style";
import { useRef,useContext } from "react";
import NavBar from "../components/NavBar";
import { motion } from "framer-motion"

const Tienda = () => {

    const carta1 = useRef(null)
    const titulo1 = useRef(null)
    const descripcion1 = useRef(null)
    const size1 = useRef(null)

    var params = useParams();
    var {id} = params


    const shop = Data.find(item => item.id==id)


    const handleMouseMove = (e) =>{
        let xAxis = (window.innerWidth / 2 - e.pageX) / 30;
        let yAxis = (window.innerHeight / 2 - e.pageY) / 30;
        carta1.current.style.transform = `rotateY(${xAxis}deg) rotateX(${yAxis}deg)`;
    }
    const handleMouseEnter = (e) => {
        carta1.current.style.transition = "none";
        titulo1.current.style.transform = "translateZ(150px)";
        descripcion1.current.style.transform = "translateZ(125px)";
        size1.current.style.transform = "translateZ(100px)";
    }
    const handleMouseLeave = (e) => {
        carta1.current.style.transition = "all 0.5s ease";
        carta1.current.style.transform = `rotateY(0deg) rotateX(0deg)`;
        titulo1.current.style.transform = "translateZ(0px)";
        descripcion1.current.style.transform = "translateZ(0px)";
        size1.current.style.transform = "translateZ(0px)";
    }
     

    return (
        <>
            <div className="fluid-container"><NavBar/></div>
            <motion.div 
            initial="out"
            animate="in"
            exit="out" variants={animacionMotion} className="mb-5 mt-5 contenedor1" style={contenedor}>
                <div style={carta} ref={carta1} onMouseMove={handleMouseMove} onMouseEnter={handleMouseEnter} onMouseLeave={handleMouseLeave} >
                    <div style={sneaker} >
                        <div style={circle}></div>
                        <img src={shop.icono} style={sneakerImg} alt={"logo de "+shop.nombre}/>
                    </div>
                    <div className="info">
                        <h1 style={infoH1} ref={titulo1}> {shop.nombre}</h1>
                        <h3 style={infoH3} ref={descripcion1}>{shop.descripcion}</h3>
                        <div style={sizes} ref={size1} >
                        <a href={shop.redesSociales[0]} style={sizesButton}><i style={botonFace} className="fab fa-facebook-square"></i></a>
                        <a href={shop.redesSociales[1]} style={sizesButton}><i style={botonInsta} className="fab fa-instagram-square"></i></a>
                        </div>
                    </div>
                </div>
            </motion.div>
            <div className="container">
                <div className="row">
                {shop.productos.map((item,index) => (
                    <div className="card col-6 my-1" key={"producto" + index}>
                     <  img src={shop.fotos[index]} style={fixSize} alt={"imagen de " + item} />
                        <div className="card-body">
                            <h5 className="card-title">{item}</h5>
                            <p className="card-text">{shop.precio[index]}</p>
                        </div>
                    </div>   
                ))}
                </div>
            </div>
        </>
    )
}

export default Tienda
