import { Link } from "react-router-dom"
import {label,caja,avatar,titulo,input,submit,enlace,fix,animacionMotion} from "../components/style"
import { useFetch } from "../components/hooks/useFetch"
import { useState,useContext } from "react"
import Swal from 'sweetalert2'
import { UserContext } from "../context/UserProvider"
import { motion } from "framer-motion"
import Usuarios from "./usuario.json"


const SignIn = () => {




    const initialState = {
      nombre:"ivan",
      contrasea:"matias",
    }
    
    
    const [usuario,setUsuario] = useState(initialState)
    

    const cambiarUsuario = (e) =>{
      setUsuario({
        ...usuario,
        [e.target.name]:e.target.value,
      })
    }
    const Toast = Swal.mixin({
      toast: true,
      position: 'top-start',
      showConfirmButton: false,
      timer: 3000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener('mouseenter', Swal.stopTimer)
        toast.addEventListener('mouseleave', Swal.resumeTimer)
      }
    })
    

    const handleMouseEnter = e => {
        e.target.style.color = "white"
      }
    
    const handleMouseLeave = e => {
        e.target.style.color = "darkgrey"
      }

    const handleSubmit= (e) => {
        e.preventDefault();
      }

    const {signIn,changeName} = useContext(UserContext)

    const handleClick = (e) => {
        //pregunto si el campo nombre esta vacio
        if(!usuario.nombre.trim()){
          Swal.fire(
            'Texto vacio',
            'Por favor ingrese un nombre',
            'error'
          )
          e.preventDefault()
          return
          }
        //pregunto si el campo contrasea esta vacio
        if(!usuario.contrasea.trim()){ 
          Swal.fire(
            'contrasea vacia',
            'Por favor ingrese una contrasea',
            'error'
          )
          e.preventDefault()
          return
        }
        //verifica si el usuario y contrasea esta en la "base de datos"
        if((Usuarios.find(item => item.nombre === usuario.nombre )) && (Usuarios.find(item => item.contrasea === usuario.contrasea))){
          signIn()
          changeName(usuario.nombre)
          return Toast.fire({
            icon: 'success',
            title: `Bienvenido ${usuario.nombre}`
          })
        }else{
          e.preventDefault()
          return Swal.fire({
            position: 'bottom-start',
            icon: 'warning',
            title: 'El usuario ingresado no existe, debe registrarse',
            showConfirmButton: false,
            timer: 2000
          })
            
        } 
    }
    const registrarse = () => {
      Toast.fire({
        icon: 'warning',
        title: 'Me encantaria poder registrarte, pero la base de datos es un JSON .',
        timer:5000
      })
    }

    return (
        
        <motion.div 
          initial="out"
          animate="in"
          exit="out" variants={animacionMotion} style={caja}>
            <img style={avatar} src="https://pngimg.com/uploads/pizza/pizza_PNG43982.png" alt="logo-pizza" />
            <div style={fix}>

            <h1 style={titulo}>Ingrese a su cuenta</h1>
            <form onSubmit={handleSubmit}>
                {/* USERNAME */}
                <label htmlFor="username" style={label}>Usuario</label>
                <input type="text" name="nombre" value={usuario.nombre} onChange={cambiarUsuario} id="username" style={input}/>
                {/* PASSWORD */}
                <label htmlFor="password" style={label}>Contrasea</label>
                
                <input type="password" name="contrasea" value={usuario.contrasea} id="password" onChange={cambiarUsuario} style={input}/>

                {/* BOTON SUBMIT */}
                <Link onClick={handleClick} to="/inicio"><input type="submit" value="Ingresar" style={submit} /></Link>

                <p style={enlace} onClick={registrarse} onMouseEnter={handleMouseEnter} onMouseLeave={handleMouseLeave}>No tenes un cuenta?</p>
            </form>


            </div>
            
        </motion.div>
        
    )
}


export default SignIn
